.dropdown {
  position: absolute;
  top: 40px;
  right: 0;
  width: 100px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
}

.dropdown button {
  display: block;
  padding: 10px;
  border: none;
  background-color: transparent;
  width: 100%;
  text-align: left;
}

.dropdown button:hover {
  background-color: #f5f5f5;
  cursor: pointer;
}


.cusrow{
  display: flex;
  justify-content: center;
  align-items: center;
}
.card{
  transition: transform 0.3s ease-in-out;
}

.card-row {
  display: flex;
  overflow: hidden; /* hide overflowing elements */
  transition: transform 0.3s ease-in-out; /* add transition effect */
}

.card-row.animate {
  transform: translateX(-100%); /* move left by container width */
}

.card {
  flex-basis: 25%;
  /* height: 100px;
