* {
    margin: 0px;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;

}
html {
    scroll-behavior: smooth;
  }


.heading {
    display: flex;
    background-color: #232f3e;
    box-shadow: 0px 1px 2px #232f3e;

}
h1 {
    color: coral;
    font-weight: bold;

    background: transparent;
    padding: 7px;

}
.ac_n{
    margin: 0 0 0px!important;
    font-size: 15px;
    color: rgb(206, 0, 93);
    font-weight: bold;
}
.ac_s{
    margin: 0 0 0px!important;
    font-size: 12px;
    font-weight: bold;
}
.prevDue{
    display: flex;
    justify-content: end;
    font-size: 18px;
    font-weight: 100;
    margin-right: 15px;
}
.prev_amount{
    color: red;
}
.outer-wrapper {
    /* margin: 10px; */
    margin-left: 20px;
    margin-right: 20px;
    /* border: 1px solid black; */
    /* border-radius: 4px;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.9); */
    max-width: fit-content;
    max-height: fit-content;


}
.table-wrapper {
/* 
    overflow-y: scroll; */
    overflow-x: scroll;
    height: fit-content;
    max-height: 66.4vh;

    margin-top: 22px;

    margin: 15px;
    padding-bottom: 20px;

}


table {

    min-width: min-content;

    border-collapse: separate;
    border-spacing: 0px;    

}



table th{
    position: sticky; 
    top: 0px;

    background-color: rgb(4, 58, 106);
    color: rgb(255, 255, 255);
font-weight: bold;
    text-align: center;
    /* font-weight: normal; */
    font-size: 14px;
    border: 1px solid rgb(145, 144, 144);

} 



table th, table td {

    padding: 8px;
    /* padding-top: 10px;
    padding-bottom: 10px; */

}

table td {
    /* text-align: center; */
    font-weight: bold;
    font-size: 15px;
    border: 1px solid rgb(0, 0, 0);
    /* padding-left: 20px; */

}

.due{
    /* border: 1px red solid; */
    font-weight: bold;
    font-size: 10px;
    /* padding: 10px; */
    padding-right: 20px;
    display: flex;
    justify-content: right;
    align-items: center;
}

.table>tbody>tr>td, .table>tbody>tr>th, .table>tfoot>tr>td, .table>tfoot>tr>th, .table>thead>tr>td, .table>thead>tr>th {
    padding: 5px;
    font-size: 13px;
    /* line-height: 1.42857143; */
    /* vertical-align: top; */
    /* border-top: 1px solid #ddd; */
}